.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f0f0f0;
}

.button-container {
    display: flex;
    gap: 20px;
}

.popup-button {
    width: 100px;
    height: 100px;
    font-size: 36px;
    font-weight: bold;
    color: white;
    background-color: #007bff;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease-in-out;
}

.popup-button:hover {
    transform: scale(1.1);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
}
